	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/minimal-components-for-when-you-dont-feel-like-using-flex/index.md absPath of file >>> MarkdownRemark","html":"<p>Nothing wrong with Flex of course, but <a href=\"http://markstar.co.uk/blog/2010/flashplatform/actionscript/hype-shapelayout-experiment/\">Mark's experiments</a> got me thinking about using <a href=\"http://www.bit-101.com/minimalcomps/\">Bit-101's MinimalComps</a> [<a href=\"http://code.google.com/p/minimalcomps/\">google code</a>] all over again.  What are they? Well, they're a set of simple components - Buttons, Checkboxes, basic containers - that are very lightweight in terms of the the size they add to your final SWF, and the processing power that's needed to render them.</p>\n<p>Why use the components? Well the Flex framework gets a bad rap for being pretty hefty on the download, and can be chuggy on the processor too, if you're not careful.  MinimalComps doesn't have this problem because it doesn't require the Flex framework, and is pretty much the epitomy of minimalist design, which looks cool when you're throwing your tech demos out there.</p>\n<p>Well, I thought I'd give it a shout out!</p>","frontmatter":{"title":"Minimal components: For when you don't feel like using Flex.","date":"February 23, 2010","tags":["flash","minimalcomps"]}}},"pathContext":{}}

